import sourceData from '../../../data-processors/sources';

export default {
    namespaced: true,
    state: {
        sources: sourceData.sources,
        countryList: sourceData.countryList,
    },
    getters: {
        currentSource: (state, getters, rootState, rootGetters) => {
            return rootGetters['currentSource/source'];
        },
        getSource: state => sourceId => {
            return state.sources[sourceId];
        },
        getSortedSourcesByCountry: (state, getters) => countryId => {
            const sourceIds = state.countryList[countryId];
            let sources = [];

            for(const sourceId of sourceIds) {
                sources.push(getters.getSource(sourceId));
            }

            sources.sort((a, b) => {
                if(a.sort > b.sort) return -1;
                if(a.sort < b.sort) return 1;
                return 0;
            });

            return sources;
        },
        getPrioSourceByCountry: (state, getters) => countryId => {
            return getters.getSortedSourcesByCountry(countryId)[0];
        },
        getPrioSourceForAllCountries: (state, getters) => {
            const countryIds = getters.getSortedCountries();
            let countries = [];

            for(const countryId of countryIds) {
                countries.push(getters.getPrioSourceByCountry(countryId));
            }

            return countries;
        },
        getSortedCountries: (state, getters) => (order = 'cases') => {
            let countries = Object.keys(state.countryList);

            countries = countries.sort((a, b) => {
                const sourceA = getters.getPrioSourceByCountry(a);
                const sourceB = getters.getPrioSourceByCountry(b);

                if(order === 'cases') {
                    return sourceA.data.stats.cases > sourceB.data.stats.cases ? -1 : 1;
                } else if(order === 'asc') {
                    if(sourceA.countryName > sourceB.countryName) return 1;
                    if(sourceA.countryName < sourceB.countryName) return -1;
                    return 0;
                }

            });

            return countries;
        }
    },
    mutations: {},
    actions: {},
};