import Vue from 'vue'
import App from './App.vue'
import store from './store/Store'

Vue.config.productionTip = false

Vue.use(store)

if(!window.document.documentMode) {

    new Vue({
        store,
        render: h => h(App),
    }).$mount('#app')

} else {
    window.document.body.innerHTML = '<a href="https://browse-better.com/">IE not supported</a>';
}