<template>

    <div>
        <canvas id="name" ref="chart"></canvas>
    </div>

</template>

<script>
    import Chart from 'chart.js';

    export default {
        name: "NakedChart",
        props: ['data', 'dataFn', 'colour'],
        data() {
            return {
                chart: null,
            }
        },
        watch: {
            data() {
                this.chart.data = this.chartData;
                this.chart.update();
            }
        },
        computed: {
            chartData() {
                return {
                    labels: this.data.map(this.dataFn),
                    datasets: [{
                        data: this.data.map(this.dataFn),
                        backgroundColor: this.colour,
                        borderColor: 'transparent'
                    }]
                }
            }
        },
        mounted() {
            const chartOptions = {
                //maintainAspectRatio: false,
                //responsive: true,
                layout: {
                    padding: {
                        left: 0,
                        right: 0,
                        top: 0,
                        bottom: 0
                    }
                },
                elements: {
                    point:{
                        radius: 0
                    }
                },
                legend: {
                    display: false,
                },
                tooltips: {
                    enabled: false
                },
                animation: false,
                scales: {
                    xAxes: [{
                        ticks: { display: false },
                        gridLines: {
                            display: false,
                            drawBorder: true
                        }
                    }],
                    yAxes: [{
                        ticks: { display: false },
                        gridLines: {
                            display: false,
                            drawBorder: true
                        }
                    }]
                }
            };

            this.chart = new Chart(this.$refs.chart, {
                type: 'line',
                options: chartOptions,
                data: this.chartData,
            });

        }
    }
</script>

<style lang="scss" scoped>

    canvas {
        @media (min-width: 1100px) {
            width: 600px;
        }
    }

</style>