export function makeTrend(days, index, dayCount) {
    if(typeof dayCount !== 'number') {
        dayCount = 7;
    }

    const trendData = [];
    for (let dayi = days.length - 1; dayi >= 0; dayi -= dayCount) {
        let count = 0;
        for(let bdayi = dayi; bdayi > dayi - dayCount; --bdayi) {
            const data = days[bdayi];
            if(data) {
                count += data[index];
            }
        }
        trendData.push({
            [index]: count,
            date: days[dayi].date
        })
    }

    trendData.reverse();
    return trendData;
}