<template>
    <div>
        <div class="tabs">
            <div v-for="tab in tabs"
                 @click="selectedId = tab.id"
                 :key="tab.id"
                 class="tab"
                 :class="[tab.id === selectedId ? '--selected' : '']"
            >{{ tab.name }}</div>
        </div>
        <div class="content">
            <div v-for="tab in tabs" :key="tab.id">
                <slot v-if="tab.id === selectedId" :name="tab.id"></slot>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "Tabs",
        props: ['tabs'],
        data() {
            return {
                selectedId: null
            }
        },
        watch: {
            tabs: {
                immediate: true,
                handler() {
                    if(this.selectedId === null) {
                        this.selectedId = this.tabs[0].id;
                    }
                }
            },
        }
    }
</script>

<style lang="scss" scoped>

    .tabs {
        margin-bottom: 15px;

        .tab {
            display: inline-block;
            border-bottom: 3px solid transparent;
            padding: 5px 10px;
            font-size: 15px;

            &.--selected {
                border-color: #706e8a;
            }
        }
    }

</style>