<template>

    <div>
        <canvas id="name" ref="chart"></canvas>
    </div>

</template>

<script>
    import Chart from 'chart.js';
    import EventBus from '../EventBus';

    export default {
        name: "Chart",
        props: ['data', 'labelFn', 'dataFn', 'type', 'tooltipTitleFn', 'colour'],
        data() {
            return {
                chart: null,
            }
        },
        watch: {
            data() {
                this.chart.data = this.chartData;
                this.chart.update();
            }
        },
        computed: {
            chartData() {
                return {
                    labels: this.data.map(this.labelFn),
                    datasets: [{
                        label: this.name,
                        data: this.data.map(this.dataFn),
                        backgroundColor: this.colour
                    }]
                }
            }
        },
        mounted() {
            const chartOptions = {
                legend: {
                    display: false,
                },
                // maintainAspectRatio: false,
                // responsive: true,
                tooltips: {
                    mode: 'index',
                    intersect: false,
                    /*
                    callbacks: {
                        title: this.tooltipTitleFn,
                    }
                    */
                },
                animation: false,
                /*scales: {
                    xAxes: [{
                        ticks: { display: false },
                        gridLines: {
                            display: false,
                            drawBorder: true
                        }
                    }],
                    yAxes: [{
                        ticks: { display: false },
                        gridLines: {
                            display: false,
                            drawBorder: true
                        }
                    }]
                }*/
            };

            this.chart = new Chart(this.$refs.chart, {
                type: this.type,
                options: chartOptions,
                data: this.chartData
            });

        }
    }
</script>

<style lang="scss" scoped>

    canvas {
        @media (min-width: 1100px) {
            width: 600px;
        }
    }

</style>