
export default {
    namespaced: true,
    state: {
        navIsOpen: false,
    },
    getters: {},
    mutations: {
        toggleNav(state) {
            state.navIsOpen = !state.navIsOpen;
        },
        closeNav(state) { state.navIsOpen = false; },
        openNav(state) { state.navIsOpen = true; },
    },
    actions: {},
};