<template>
    <div class="source-picker">
        <div  v-for="sibSource of sources"
              :key="sibSource.sourceId"
              :class="[sibSource.sourceId === source.sourceId ? '--current' : '']"
              class="source"
              @click="$store.commit('currentSource/set', sibSource.sourceId)"
        >
            <div class="bg"></div>
            <div class="text">
                {{ sibSource.sourceName }}
            </div>
        </div>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex';

    export default {
        name: "SourcePicker",
        computed: {
            ...mapGetters('currentSource', ['source']),
            ...mapGetters('sources', ['getSortedSourcesByCountry']),

            sources() {
                return this.getSortedSourcesByCountry(this.source.countryId);
            }
        }
    }
</script>

<style lang="scss" scoped>

    .source-picker {

    }

    .source {
        display: inline-block;
        padding: 4px 10px;
        border-radius: 100px;
        font-size: 14px;
        margin-right: 10px;
        white-space: nowrap;
        position: relative;
        overflow: hidden;
        box-shadow: inset 0 0 0 1px rgba(255,255,255,.8);
        color: rgba(255,255,255,.8);

        .bg {
            position: absolute;
            top: 0;
            left: 0;
            bottom: 0;
            right: 0;
            background: #FFF;
            z-index: 0;
            border-radius: 100px;
            transform: translateX(-100%);
            transition-duration: inherit;
            transform-origin: left center;
        }

        .text {
            position: relative;
            z-index: 1;
        }

        &.--current {
            color: #333;
            transition-duration: .3s;

            .bg {
                transform: none;
            }
        }
    }

</style>