<template>
    <div class="sidebar-wrapper" :class="[navIsOpen ? '--show-sidebar' : '']">
        <div class="sidebar">
            <input class="filter-input" v-model="countryFilterValue" type="text" placeholder="Filter countries">
            <ul class="country-list" ref="countryList">
                <template v-for="countrySource of [pinnedCountries, filteredCountries]">
                    <li v-for="source in countrySource"
                        :key="source.countryId"
                        :class="[isCurrentCountry(source.countryId) ? '--is-current' : '']"
                    >
                        <div class="data" @click="setSource(source.sourceId)">
                            <div class="name">
                                {{ source.countryName }}
                            </div>
                            <div class="meta">
                        <span style="display: inline-block; width: 120px">
                            {{ formatNumber(source.data.stats.cases) }} cases <trend-arrow :number="source.data.stats.casesTrend"></trend-arrow>
                        </span>
                                {{ formatNumber(source.data.stats.deaths) }} deaths <trend-arrow :number="source.data.stats.deathsTrend"></trend-arrow>
                            </div>
                        </div>
                        <div class="pin" @click="toggleCountryPin(source.countryId)" :class="[isPinned(source.countryId) ? '--is-pinned' : '']">
                            <span :class="[isPinned(source.countryId) ? 'icon-bookmark' : 'icon-bookmark-outline-add']"></span>
                        </div>
                    </li>
                </template>
            </ul>
        </div>
        <div class="underlay" @click="$store.commit('navigation/closeNav')"></div>
    </div>
</template>

<script>
    import { mapState, mapGetters } from 'vuex';

    import { formatNumber } from '../helpers';

    import TrendArrow from './TrendArrow';


    export default {
        name: "Sidebar",
        components: { TrendArrow },
        data() {
            return {
                countryFilterValue: '',
                pinned: [],
            }
        },
        watch: {
            navIsOpen() {
                if(this.navIsOpen) {
                    this.countryFilterValue = '';
                    this.$refs.countryList.scrollTop = 0;
                }
            },
            pinned() {
                const json = JSON.stringify(this.pinned);
                window.localStorage.setItem('pinnedCountries', json);
            },
        },
        mounted() {
            try {
                const saved = JSON.parse(window.localStorage.getItem('pinnedCountries'));
                if(Array.isArray(saved)) {
                    this.pinned = saved;
                }
            } catch(e) {}
        },
        computed: {
            ...mapState('navigation', ['navIsOpen']),
            ...mapGetters('sources', ['getPrioSourceForAllCountries', 'getPrioSourceByCountry']),
            ...mapGetters('currentSource', ['isCurrentCountry']),

            filteredCountries() {
                return this.getPrioSourceForAllCountries.filter(country => {
                    return this.isPinned(country.countryId) ? false : country.countryName.toLowerCase().indexOf(this.countryFilterValue.toLowerCase()) !== -1;
                })
            },
            pinnedCountries() {
                let pinned = [];
                for(const countryId of this.pinned) {
                    pinned.push(this.getPrioSourceByCountry(countryId));
                }
                return pinned;
            },
        },
        methods: {
            formatNumber,
            setSource(sourceId) {
                this.$store.commit('navigation/closeNav');
                this.$store.commit('currentSource/set', sourceId);
            },
            toggleCountryPin(countryId) {
                if(!this.isPinned(countryId)) {
                    this.pinned.push(countryId);
                } else {
                    this.pinned.splice(this.pinned.indexOf(countryId), 1);
                }
            },
            isPinned(countryId) {
                return this.pinned.includes(countryId);
            },
        }
    }
</script>

