<template>
    <div class="r" :style="{ transform: 'rotate('+ degrees +'deg)', color: colour }">
        <span class="icon-arrow-right"></span>
    </div>

</template>

<script>
    export default {
        name: "TrendArrow",
        props: ['number'],
        computed: {
            degrees() {
                let number = (this.number - 1) * -90;
                if(number > 45) number = 45;
                if(number < -45) number = -45;
                return number;
            },
            colour() {
                return this.degrees < 0 ? '#ff4f4f' : '#5ce45c';
            }
        }
    }
</script>

<style lang="scss" scoped>
    .r {
        display: inline-block;
    }
</style>