
export default {
    namespaced: true,
    state: {
        currentSourceId: window.location.hash ? window.location.hash.substring(1) : 'australia_edec',
    },
    getters: {
        source: (state, getters, rootState, rootGetters) => {
            return rootGetters['sources/getSource'](state.currentSourceId);
        },
        stats: (state, getters) => {
            return getters.source.data.stats;
        },
        isCurrentCountry: (state, getters) => countryId => {
            return getters.source.countryId === countryId;
        },
        todaysStats: (state, getters) => {
            const days = getters.source.data.days;
            return {
                newCases: days[days.length-1].newCases,
                newDeaths: days[days.length-1].newDeaths,
            }
        },
        daysInRange: (state, getters, rootState) => {
            const days = getters.source.data.days;
            return days.slice(days.length - rootState.main.daysToShow);
        },
        // Todo: Clean up this mess
        days: (state, getters) => range => {
            const days = getters.source.data.days;
            if(!range) return days;
            range = range > 0 ? range : 1;
            range = range < 0 ? 1 : range;
            return days.slice(days.length - range);
        },
    },
    mutations: {
        set(state, sourceId) {
            state.currentSourceId = sourceId;
            window.history.replaceState(null, '', '#' + sourceId);
        }
    },
    actions: {},
};